.themes {
  padding-bottom: $pb-section-desctop;
  &__inner {}
  &__title {}
  &__subtitle {
    font-weight: 500;
    font-size: 18px;
    color: $color-subtitle;
    margin-bottom: 60px;
  }
  &__slider {
    margin-bottom: 60px;
  }
  &__list {}
  &__btn {
    margin: 0 auto;
    // display: block;
  }
}

.themes-card {
  // box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  padding: 30px 40px 30px 25px;
  transition: box-shadow .15s;
  &.is-shadow {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  }
  &.is-visible {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    color: #3E4DA6;
    margin-bottom: 20px;
  }
  &__text {
    line-height: 24px;
    color: #6C6A72;
  }
}



@media (min-width: 1200px) {

}
@media (min-width: 992px) and (max-width: 1199px) {

}
@media (min-width: 768px) and (max-width: 991px) {
  .themes {
    padding-bottom: $pb-section-tablet;
  }
}
@media (max-width: 767px) {
  .themes {
    padding-bottom: $pb-section-mobile;
    &__subtitle {
      font-size: 16px;
      margin-bottom: 35px;
    }
    &__slider {
      margin-bottom: 30px;
    }
  }
  .themes-card {
    padding: 20px 10px;
    &__title {
      font-size: 16px;
      margin-bottom: 15px;
    }
    &__text {
      font-size: 14px;
      line-height: 21px;
    }
  }
}
