@media (min-width: 768px) and (max-width: 991px) {
  .results {
    padding-bottom: $pb-section-tablet;
  }
  .results-card {
    &__header {
      height: 60px;
    }
    &__info {
      max-width: 190px;
    }
  }
}
@media (max-width: 767px) {
  .results {
    padding-bottom: $pb-section-mobile;
    &__list {
      width: 100%;
    }
    &__slider {
      width: 100% !important;
      margin-left: 0 !important;
      & .splide__track {
        padding-bottom: 0;
      }
    }
    &__btn {
      display: block;
    }
  }
  .results-card {
    width: 100%;
    margin-bottom: 20px;
    &::before {
      font-size: 30px;
    }
    &__header {
      margin-bottom: 15px;
    }
    &__name {
      font-size: 16px;
    }
    &__box {
      padding: 20px 15px 15px;
    }
    &__info {
      font-size: 14px;
      line-height: 21px;
      max-width: 190px;
    }
    &__text {
      // font-size: 16px;
      // line-height: 24px;
    }
  }
}
