.reason {
  padding-bottom: $pb-section-desctop;
  &__inner {
    // position: relative;
  }
  &__title {
    margin-bottom: 30px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-right: 180px;
    padding-bottom: 85px;
  }
  &__text {
    position: relative;
    background-color: #F6F8FF;
    line-height: 24px;
    border-top-right-radius: 150px;
    padding: 30px 350px 30px 0;
    & > p {
      margin-bottom: 30px;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      background-color: #F6F8FF;
      width: 50%;
      left: -100px;
      top: 0;
      height: 100%;
    }
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 485px;
  }
}



@media (max-width: 1199px) {
  .reason {
    &__content {
      padding-right: 100px;
    }
    &__text {
      padding-right: 310px;
    }
    &__img {
      max-width: 400px;
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .reason {

  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .reason {
    padding-bottom: $pb-section-tablet;
    &__content {
      padding-right: 0;
      padding-bottom: 0;
    }
    &__text {
      padding-right: 50px;
      padding-left: 30px;
      border-top-right-radius: 100px;
      border-bottom-left-radius: 100px;
      &::after {
        display: none;
      }
    }

    &__img {
      display: none;
    }
  }
}
@media (max-width: 767px) {
  .reason {
    padding-bottom: $pb-section-mobile;
    &__content {
      padding: 0;
    }
    &__text {
      width: calc(100% + 20px);
      margin-left: -10px;
      padding: 20px 10px;
      font-size: 14px;
      line-height: 21px;
      border-radius: 0;
      & > p:last-child {
        margin-bottom: 0;
      }
      &::after {
        display: none;
      }
    }
    &__img {
      display: none;
    }
  }
}
