@media (max-width: 767px) {
  .news {
    padding-bottom: $pb-section-mobile;
  }
  .news-grid {
    &__content {
      top: 20px;
      left: 15px;
    }
    &__title {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 8px;
    }
    &__subtitle {
      font-size: 14px;
      line-height: 21px;
    }
  }
}
@media (max-width: 600px) {
  .news {
    &__slide {
      margin-right: 20px;
    }
  }
  .news-grid {
    &__item {
      // display: block;
      flex-basis: 48%;
      max-width: none;
      flex-shrink: 0;
      flex-grow: 1;
      margin-right: 20px;
      padding-bottom: 98%;
    }
    // height: 400px;
  }
}
@media (max-width: 450px) {
  .news-grid {
    &__item {
      // display: block;
      // flex-basis: 48%;
      // max-width: none;
      // flex-shrink: 0;
      // flex-grow: 1;
      flex-basis: 100%;
      margin-right: 20px;
      padding-bottom: 90%;
    }
  }
}