.chart {
  padding-bottom: $pb-section-desctop;
  overflow: hidden;
  &__inner {}
  &__title {}
  &__content {
    display: flex;
    justify-content: space-between;
    background: #F7F8FF;
    position: relative;
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      top: 0;
      right: -200px;
      height: 100%;
      width: 50%;
      background: #F7F8FF;
    }
  }
  &__board {
    flex-basis: 50%;
    max-width: 50%;
    background-color: #ffffff;
  }
  &__text {
    flex-basis: 50%;
    max-width: 50%;
    padding: 30px;
    & > p {
      margin-bottom: 30px;
    }
    & > p:last-child {
      margin-bottom: 0;
    }
    & b {
      font-weight: 500;
      font-size: 18px;
    }
  }
}

.chart-board {
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  border-radius: 10px;
  padding: 25px 15px 50px 50px;
  // padding-top: 50px;
  padding-bottom: 100px;
  position: relative;
  &__title {
    padding-bottom: 25px;
    font-weight: 500;
    color: $color-subtitle;
    font-size: 18px;
  }
  &__box {
    // margin-top: 25px;
    border-bottom: 1px solid #A7A5AD;
    border-left: 1px solid #A7A5AD;
    height: 100%;
    position: relative;
    &::before, &::after {
      position: absolute;
      display: block;
      color: #A7A5AD;
    }
    &::before {
      content: attr(data-y);
      left: -20px;
      top: 0;
      transform-origin: left;
      transform: rotate(-90deg) translateX(-100%);
    }
    &::after {
      content: attr(data-x);
      bottom: -30px;
      right: 0;
    }
  }
  &__line {
    background-image: url(../img/content/chart-line.svg);
    background-repeat: no-repeat;
    background-position: 0 bottom;
    background-size: 100% 70%;
    height: 100%;
    width: 100%;
    position: absolute;
  }
  &__icon {
    background-image: url(../img/icon/angry.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    height: 32px;
    width: 32px;
    position: absolute;
    right: 6%;
    top: 25%;
    &::before {
      content: attr(data-text);
      position: absolute;
      left: 50%;
      top: -1.5em;
      color: #C10000;
      display: inline-block;
      transform: translateX(-50%);
    }
  }
}


