      // margin-left: -10px;
      background: #F7F8FF;
      position: relative;
      &::before, &::after {
        content: '';
        display: block;
        width: 10px;
        position: absolute;
        background: #F7F8FF;
        width: 10px;
        height: 100%;
        top: 0;
      }
      &::before {
        left: -10px;
      }
      &::after {
