.popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 20;
  background-color: rgba(10, 39, 79, 0.3);
  // opacity: 0;
  // transition: .35s ease;
  // overflow: hidden;

  &__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 1030px;
    width: 100%;
    padding: 90px 70px;
    background-color: #ffffff;
    border-radius: 10px;
    // transform: scale(0.9);
  }
  & .request {
    padding-bottom: 0;
  }
  &.hidden {
    display: none;
  }
  &__close {
    width: 30px;
    height: 30px;
    background-color: transparent;
    border: none;
    border-radius: 50%;
    position: absolute;
    right: 30px;
    top: 30px;
    cursor: pointer;
    &::before, &::after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background-color: #77838B;
      position: absolute;
      top: 50%;
      left: 50%;
    }
    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}


